:root
{
    --field-border-color: #333333;
    --field-border-width: 1px;

    --cross-color: #0d51e4;
    --circle-color: #dd2020;
}

.field
{
    width: calc(33% - var(--field-border-width));
    height: calc(33% - var(--field-border-width));
    border-width: var(--field-border-width);

    display: flex;
    align-items: center;
    justify-content: center;

    transition: background-color 0.5s ease, opacity 1.5s ease;
}
.field:nth-child(2), .field:nth-child(8)
{
    border-left-style: solid;
    border-right-style: solid;
}
.field:nth-child(4), .field:nth-child(6)
{
    border-top-style: solid;
    border-bottom-style: solid;
}
.field:nth-child(5)
{
    border-style: solid;
}

.field.highlight
{
    background-color: var(--highlight);
    cursor: pointer;

}
.field.highlight:hover
{
    background-color: var(--highlight-opaque);
}


.field svg
{
    width: 80%;
    height: 80%;
}
svg[name="circle"]
{
    color: var(--circle-color);
}
svg[name="circle"].animate
{
    stroke-dasharray: 170;
    stroke-dashoffset: 170;
    animation: circleIn 0.5s cubic-bezier(.4,0,.2,1) forwards;
    transform: rotateZ(-90deg);
}

@keyframes circleIn
{
    to {
        stroke-dashoffset: 0;
    }
}

svg[name="cross"]
{
    color: var(--cross-color);
}
svg[name="cross"].animate rect:nth-child(1)
{
    width: 0;
    animation: crossIn 0.5s cubic-bezier(.4,0,.2,1) forwards;
}

svg[name="cross"].animate rect:nth-child(2)
{
    width: 0;
    animation: crossIn 0.5s cubic-bezier(.4,0,.2,1) forwards 0.1s;
}

@keyframes crossIn
{
    to {
        width: 80px;
    }
}