.game-winner
{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    animation: game-winner-in 1s forwards 0.25s;
}
@keyframes game-winner-in
{
    from { background-color: var(--highlight-transparent); }
    to { background-color: var(--highlight); }
}

.game-winner-winner-text
{
    color: white;
    font-size: 2em;
    font-weight: bold;
    opacity: 0;
    animation: game-winner-span-in 1s forwards 1s;
}
@keyframes game-winner-span-in
{
    from { opacity: 0; }
    to { opacity: 1; }
}

.game-winner svg[name="circle"].animate
{
    animation-duration: 2.5s !important;
    animation-delay: 1.25s !important;
}
.game-winner svg[name="cross"].animate rect:nth-child(1)
{
    animation-duration: 2.5s !important;
    animation-delay: 1.25s !important;
}
.game-winner svg[name="cross"].animate rect:nth-child(2)
{
    animation-duration: 2.5s !important;
    animation-delay: 2.0s !important;
}

.game-winner button
{
    margin: 0.25em 0;
    animation: game-winner-button-in 1s forwards 2.5s;
    opacity: 0;
}
@keyframes game-winner-button-in
{
    from { opacity: 0; }
    to { opacity: 1; }
}