:root
{
    --tttfield-border-color: #555555;
    --tttfield-border-width: 3px;
}

.tttfield
{
    position: relative;

    width: calc(33% - var(--tttfield-border-width));
    height: calc(33% - var(--tttfield-border-width));

    display: inline-flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    
    border-width: var(--tttfield-border-width);
}
.tttfield:nth-child(2), .tttfield:nth-child(8)
{
    border-left-style: solid;
    border-right-style: solid;
}
.tttfield:nth-child(4), .tttfield:nth-child(6)
{
    border-top-style: solid;
    border-bottom-style: solid;
}
.tttfield:nth-child(5)
{
    border-style: solid;
}

.tttfield > .field
{
    opacity: 1;
}
.tttfield.hide > .field
{
    opacity: 0.3;
}

.tttfield-winner
{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}
.tttfield-winner .field
{
    width: 100%;
    height: 100%;
}
.tttfield-winner svg[name="circle"].animate
{
    animation-duration: 1.5s !important;
    animation-delay: 0.25s !important;
}
.tttfield-winner svg[name="cross"].animate rect:nth-child(1)
{
    animation-duration: 1.25s !important;
    animation-delay: 0.25s !important;
}
.tttfield-winner svg[name="cross"].animate rect:nth-child(2)
{
    animation-duration: 1.25s !important;
    animation-delay: 0.55s !important;
}