.game {
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.game-gamefield-wrapper
{
    width: 70vh;
    height: 70vh;
    transition: opacity 0.5s ease;
}
.game-gamefield-wrapper.disabled:hover
{
    opacity: 0.3;
}
.game-gamefield-wrapper.disabled *
{
    cursor: not-allowed;
}