:root
{
    --gamehistory-gamefield-background: #aaa4;
    --gamehistory-gamefield-background-selected: #ffa50080;
}

.gamehistory-scroll-wrapper
{
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    width: 95vw;
    border-radius: 1em;
}

.gamehistory
{
    flex: 0 0 auto;
    white-space: nowrap;
    
    min-width: 95%;

    height: 8em;
    
    background-color: rgba(100,100,100,.1);

    transition: background-color 1.5s ease;
    
    padding: 1em;
    
}

.gamehistory-gamefield-wrapper
{
    display: inline-block;
    position: relative;

    width: 7em;
    height: 7em;

    margin: 0 1em;
    
    transition: background-color 0.5s ease, transform 0.25s cubic-bezier(0.25, 0, 0.25, 1.8);
    
    cursor: pointer;

    background-color: var(--gamehistory-gamefield-background);

    border-radius: 0.5em;
}
.gamehistory-gamefield-wrapper.selected
{
    background-color: var(--gamehistory-gamefield-background-selected);
}
.gamehistory-gamefield-wrapper:hover,
.gamehistory-gamefield-wrapper.selected
{
    transform: translateY(-0.8em);
}

.gamehistory-gamefield-number-wrapper
{
    display: flex;
    justify-content: right;

    top: 0;
    position: absolute;

    width: calc(100% - 0.3em);
    height: 2.25em;

    overflow: hidden;
    
    mask-image: linear-gradient(to bottom, #ffff, #ffff 90%, #0000 100%);
    mask-mode: alpha;
}
.gamehistory-gamefield-number
{
    user-select: none;

    width: 100%;
    height: 2.25em;

    font-size: 2.25em;
    line-height: 100%;
    text-align: right;
    font-weight: bold;
    color: white;

    -webkit-text-stroke: 1px #0009;

    transform: translate(0, 75%);

    transition: transform .5s cubic-bezier(0.25, 0, 0.25, 1.2), opacity .25s ease;
}
.gamehistory-gamefield-wrapper:hover .gamehistory-gamefield-number
{
    opacity: .25;
}

.gamehistory-scroll-wrapper:hover .gamehistory-gamefield-number,
.game.viewing-history .gamehistory-gamefield-number
{
    transform: none;
}

.gamehistory .scroller-filler
{
    display: inline-block;
}


.gamehistory-scroll-wrapper::-webkit-scrollbar
{
    width: 10px;
    height: 10px;
    background-color: #0000;
    border-radius: 20px;
}

.gamehistory-scroll-wrapper::-webkit-scrollbar-thumb
{
  background-color: #fffa;
  border-radius: 20px;
}