:root
{
    --background-chars-width: 40vh;
    --background-chars-height: 40vh;
    --background-chars-margin-top: 30vh;
    
}

.background
{
    position: absolute;

    width: 100%;
    height: 100%;

    z-index: -1;

    overflow: hidden;
}

.background-char {
    position: absolute;

    width: var(--background-chars-width);
    height: var(--background-chars-height);
    
    margin-top: var(--background-chars-margin-top);
    
    transition: opacity 0.5s ease, color 0.5s ease;

    opacity: 0.3;
}
.background-char.selected
{
    opacity: 1;
}
.background-char svg
{
    color: #000;
    width: 100%;
    height: 100%;
}

.background-char.cross
{
    transform: rotate(20deg) translateX(-10vh);
}
.background-char.cross.selected svg
{
    color: var(--cross-color);
 
}


.background-char.circle
{
    margin-left: calc(100vw - 25vh);
    
    transform: rotate(-20deg);    
}
.background-char.circle.selected svg
{
    color: var(--circle-color);
}

.background-solid
{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #333;
    z-index: -5;
    transition: background-color 2s ease;
}
.game.viewing-history .background-solid
{
    background-color: #ffe4b3;
}
.background-gradient
{
    position: absolute;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, #fff8, #ffff);
    z-index: -4;

    transition: transform 1.5s ease-out, opacity 1s ease 0.3s;
}

.game.viewing-history .background-gradient
{
    transform: scale(5,5);
}